import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Login from './Login';
import Gestor from './Gestor';
import NotFoundPage from './NotFoundPage';
import ResponsiveAppBar from './ResponsiveAppBar';
const usuarioSeleccionado = {
  usuario: '',
  password: '',
  company_id: 3,
  person_id: 3,
  search: 'test',
  created_by: 2,
  updated_by: 2
}

const menuSeleccionado = {
  name: "",
  details: "Es un Dashboard de los usuarios",
  component: "",
  url: "",
  icon: "No definido",
  fatherMenu: 2,
  isVisible: true,
  createdBy: 1,
  createdDate: "2022-05-16T03:00:44",
  updatedBy: 1,
  updatedDate: "2022-05-16T03:00:44"
}
export default function App() {
  return (
    <Router>
      <Routes>
        <Route path='/' element={<Login />} />
        <Route path='/home' element={<ResponsiveAppBar />} />
        <Route path='/usuarios' element={<Gestor controller="Usuario" map={usuarioSeleccionado} keyTable={"user_id"} rowStatus={"user_status"} columnName1={"usuario"} columnName2={"password"} columnName3={"Actions"} />} />
        <Route path='/menus' element={<Gestor controller="Menu" map={menuSeleccionado} keyTable={"menuID"} rowStatus={"menuStatus"} columnName1={"name"} columnName2={"component"} columnName3={"Actions"} />} />
        <Route path='*' element={<NotFoundPage />} />
      </Routes>
    </Router>

  )
}

